body {
    font-family: "Myriad Pro", Arial, Helvetica, sans-serif;
    height: calc(100vh - 17px);
}

h1 {
    margin: 0 75px;
    text-align: left;
}

#container {
    text-align: left;
    margin-left: 30px;
}

#grid-container {
    display: grid;
    grid-template-columns: 0fr 0fr 1fr;
    grid-template-rows: auto 1fr;
    /* grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: auto 1fr; */
}

#scatter {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
}

#legend {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
    text-align: left;
    position: relative;
    width: min-content;
    padding: 0px 10px 5px 10px;
    border: 1px solid black;
}
#legend table {
    position: absolute;
    top: 46px;
    left: 5px;
    width: 310px;
}
#legend td {
    height: 22px;
    border: 1px solid white;
    background-color: white;
    opacity: 0;
    cursor: pointer;
}
#legend img {
    width: 300px;
}

#popup {
    grid-column: 3 / 4;
    grid-row: 2 / 3;
    text-align: left;
    font-size: 15px;
    line-height: .5;
}

circle {
    cursor: pointer;
}